import { Component, h, Prop, State, Element, Watch, Host } from '@stencil/core';
import { ColDef, GridApi } from 'ag-grid-community';
import { fetchTranslations } from '../../../utils/translation';

/**
 * @category Data Tables
 * @description A searchable data table component that combines table functionality with integrated search capabilities. Provides real-time filtering and search result highlighting.
 * @slot actions - Used to place actions related to table content
 * @slot multi-select-actions - Used to place actions related to row selection
 */
@Component({
    tag: 'atui-search-table',
    shadow: false,
})
export class AtuiSearchTable {
    /**
     * Table data passed to atui-table component.
     */
    @Prop()
    table_data: {
        items: any[];
        total: number;
    };

    /**
     * Label for the table, appears above the search input.
     */
    @Prop()
    label: string;

    /**
     * Label for the search input.
     */
    @Prop()
    search_label: string;

    /**
     * Hint text displayed below the search label.
     */
    @Prop()
    search_hint: string;

    /**
     * Info text displayed in the search info tooltip.
     */
    @Prop()
    search_info_tooltip: string;

    /**
     * Column definitions passed to atui-table component.
     */
    @Prop()
    col_defs: ColDef[];

    /**
     * Default page size of the table
     */
    @Prop()
    page_size?: number = 10;

    /**
     * If true the table dropdown filters will not be added
     */
    @Prop()
    hide_dropdown_filters?: boolean;

    /**
     * If true the column manager will not be added
     */
    @Prop()
    hide_column_manager?: boolean;

    /**
     * If true the table export menu will not be added
     */
    @Prop()
    hide_export_menu?: boolean;

    /**
     * If true, disables pagination on the table and shows all data at once.
     * Useful for server-side pagination where you want to control pagination externally.
     */
    @Prop()
    use_custom_pagination?: boolean = false;

    @Element()
    el: HTMLElement;

    @State()
    translations: any;

    @State()
    agGrid: GridApi;

    @State()
    tableCreated: boolean = false;

    @State()
    activeFilters: { [key: string]: string } = {};

    @State()
    selectedFilters: { id: string; value: string }[] = [];

    @State()
    menuSelectedIds: string[] = [];

    @State()
    searchValue: string = '';

    tableEl: HTMLAtuiTableElement;

    @Watch('selectedFilters')
    handleSelectedFiltersChange(newValue: { id: string; value: string }[]) {
        this.menuSelectedIds = newValue.map((f) => f.id);
    }

    async componentWillLoad() {
        this.translations = await fetchTranslations(this.el);
    }

    async componentDidLoad() {
        await this.initGrid();
    }

    async componentDidUpdate() {
        await this.initGrid();
    }

    private async initGrid() {
        if (this.col_defs && !this.tableCreated && this.tableEl) {
            this.agGrid = await this.tableEl.createGrid();
            this.tableCreated = true;
            this.setupExternalFilters();

            if (this.table_data?.items) {
                this.agGrid.setGridOption('rowData', this.table_data.items);
            }
        }
    }

    private setupExternalFilters() {
        if (!this.agGrid) return;

        this.agGrid.setGridOption<'isExternalFilterPresent'>(
            'isExternalFilterPresent',
            () => {
                const hasFilters = Object.keys(this.activeFilters).length > 0;
                console.log(
                    'isExternalFilterPresent called, hasFilters:',
                    hasFilters,
                );
                return hasFilters;
            },
        );

        this.agGrid.setGridOption<'doesExternalFilterPass'>(
            'doesExternalFilterPass',
            (node) => {
                if (!node.data) return true;

                const searchValue = this.activeFilters['__search__'];
                if (searchValue) {
                    const searchLower = searchValue.toLowerCase();
                    const matchesSearch = Object.values(node.data).some(
                        (value) =>
                            value &&
                            String(value).toLowerCase().includes(searchLower),
                    );
                    if (!matchesSearch) return false;
                }

                const columnFilterResult = Object.entries(
                    this.activeFilters,
                ).every(([colId, filterValue]) => {
                    if (colId === '__search__' || !filterValue) return true;
                    const value = node.data[colId];
                    const matches =
                        value &&
                        String(value)
                            .toLowerCase()
                            .includes(String(filterValue).toLowerCase());
                    return matches;
                });

                return columnFilterResult;
            },
        );
    }

    handleColumnChange(event: CustomEvent) {
        const { id, checked } = event.detail;
        const updatedColDefs = this.col_defs.map((colDef) => {
            if (colDef.field === id) {
                return { ...colDef, hide: !checked };
            }
            return colDef;
        });

        this.col_defs = updatedColDefs;
    }

    handleFilterChange(event: CustomEvent) {
        if (event.detail) {
            if (
                Array.isArray(event.detail) &&
                typeof event.detail[0] === 'string'
            ) {
                this.handleMenuFilterChange(event.detail);
            } else if (
                Array.isArray(event.detail) &&
                typeof event.detail[0] === 'object'
            ) {
                this.handleFilterListChange(event.detail);
            }
        }

        this.updateActiveFilters();
    }

    private handleMenuFilterChange(selectedIds: string[]) {
        this.selectedFilters = selectedIds.map((id) => ({
            id,
            value: '',
        }));
        this.menuSelectedIds = selectedIds;
    }

    private handleFilterListChange(filters: { id: string; value: string }[]) {
        if (filters.length === 1 && filters[0].id === '') {
            this.selectedFilters = [];
            this.menuSelectedIds = [];
        } else {
            this.selectedFilters = filters;
            this.menuSelectedIds = filters
                .filter((f) => f.value && f.value.trim() !== '')
                .map((f) => f.id);
        }
    }

    private updateActiveFilters() {
        this.activeFilters = {};
        this.selectedFilters.forEach((filter) => {
            if (filter.value) {
                this.activeFilters[filter.id] = filter.value;
            }
        });

        if (this.searchValue) {
            this.activeFilters['__search__'] = this.searchValue;
        }

        if (this.agGrid) {
            this.setupExternalFilters();
            this.agGrid.onFilterChanged();
        } else {
            console.log('agGrid not available, cannot apply filter');
        }
    }

    handleSearchChange(event: CustomEvent) {
        this.searchValue = event.detail || '';
        this.updateActiveFilters();
    }

    render() {
        return (
            <Host>
                <atui-table-actions ag_grid={this.agGrid}>
                    <div
                        class="flex items-center gap-8"
                        slot="search"
                    >
                        {!this.hide_dropdown_filters && this.col_defs && (
                            <atui-table-filter-menu
                                slot="filter-menu"
                                col_defs={this.col_defs}
                                selected={this.menuSelectedIds}
                                onAtuiChange={(event) =>
                                    this.handleFilterChange(event)
                                }
                            ></atui-table-filter-menu>
                        )}

                        <atui-search
                            class="w-input-md"
                            label={this.search_label}
                            hint_text={this.search_hint}
                            info_text={this.search_info_tooltip}
                            placeholder={
                                this.translations.ATUI.TABLE.SEARCH_BY_KEYWORD
                            }
                            onAtuiChange={(event) =>
                                this.handleSearchChange(event)
                            }
                        ></atui-search>
                    </div>

                    {!this.hide_dropdown_filters && this.col_defs && (
                        <atui-table-filters
                            slot="filters"
                            col_defs={this.col_defs}
                            selected={this.selectedFilters}
                            onAtuiChange={(event) =>
                                this.handleFilterChange(event)
                            }
                        ></atui-table-filters>
                    )}

                    {!this.hide_export_menu && (
                        <atui-table-export-menu slot="export-menu" />
                    )}

                    {!this.hide_column_manager && this.col_defs && (
                        <atui-column-manager
                            slot="column-manager"
                            col_defs={this.col_defs}
                            onAtuiChange={(event) =>
                                this.handleColumnChange(event)
                            }
                        />
                    )}

                    <div slot="actions">
                        <slot name="actions" />
                    </div>
                </atui-table-actions>

                <slot name="multi-select-actions" />

                <atui-table
                    ref={(el) => (this.tableEl = el)}
                    table_data={this.table_data}
                    col_defs={this.col_defs}
                    page_size={this.page_size}
                    use_custom_pagination={this.use_custom_pagination}
                    disable_auto_init={true}
                />
            </Host>
        );
    }
}
